___CSS_LOADER_EXPORT___.push([module.id, `:root {
  /* Light mode colors */
  --main-background-color: #fff;
  --secondary-background-color: #f4f4f4;
  --text-color: #333333;
  --secondary-text-color: #333333;
  --card-background: #f0f0f0;
  --border: 1px solid #acacaa;
  font-family: "Lato", sans-serif;
}

/* Dark mode colors */
[data-theme="dark"] {
  --main-background-color: #696969;
  --secondary-background-color: #424242;
  --text-color: #000000;
  --secondary-text-color: #000000;
  --card-background: #2c2c2c;
  color: var(--text-color);
}

body {
  margin: 0;
  background-color: var(--main-background-color);
  width: 350px;
}

#popup-container {
  display: flex;
  flex-direction: column;
}

/* top bar */
#entire-top-bar {
  background-color: var(--secondary-background-color);
  padding: 15px 15px 0px 15px;
  padding-bottom: 15px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
}

.top-bar-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.8rem;
}

.header-text {
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  color: var(--light-main-text);
  margin-left: 0.5rem;
}

.top-bar-icon {
  width: 32px;
  height: 32px;
}

/* Settings Button */
#settings-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.7s ease;

  img {
    user-select: none;
    width: 100%;
    height: 100%;
    display: block;
  }
}

#settings-icon:hover {
  cursor: pointer;
  transform: rotate(0.3turn);
}

.button-hover-effect {
  padding: 6px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

#middle-section {
  padding: 5px 15px 15px 15px;

  h1 {
    font-size: 18px;
  }
}

#bottom-section {
  background-color: var(--secondary-background-color);
  box-shadow: 0 -2px 3px rgba(0, 0, 0, 0.2);
  align-content: center;
  padding: 0px 15px;
  height: 78px;
}

#bottom-section h1 {
  font-size: 18px;
}

#bottom-section .blocked-svg-container {
  background-color: #c5ce9e00;
}

#bottom-section svg {
  width: 32px;
  height: 32px;
}

#bottom-section .badge {
  background-color: #ccccc8;
  width: 27px;
  height: 20px;
  font-size: 9px;
}

.block-url-header-container {
  display: flex;
  align-items: baseline;
}

.add-in-bulk-link {
  opacity: 0.7;
  font-size: 0.7rem;
  margin-left: 10px;
  text-decoration: underline;
}

.add-in-bulk-link:hover {
  opacity: 1;
  cursor: pointer;
}

.dicord-button {
  cursor: pointer;
}
